import Moment from 'moment';
import { extendMoment } from 'moment-range';

const moment = extendMoment(Moment);
import { useState } from 'react';
import { MonthView } from './MonthView';

export const CalendarView = ({ year, month, onCellClick }) => {
  const [firstDayOfTheMonth, setFirstDayOfTheMonth] = useState(
    moment(year.concat('-').concat(month)).startOf('month'),
  );
  const [lastDayOfTheMonth, setLastDayOfTheMonth] = useState(
    moment(year.concat('-').concat(month)).endOf('month'),
  );
  const [allMonths, setAllMonths] = useState(moment.months());

  return (
    <div className="Container">
      <div className="row">
        <h3 className="text-center">{year}</h3>
      </div>
      <div className="row">
        <ul className="nav nav-tabs">
          {allMonths.map(m => {
            return <li className=''></li>
          })}
        </ul>
      </div>
      <div className="row">
        <MonthView
          firstDay={firstDayOfTheMonth}
          endDay={lastDayOfTheMonth}
          onCellClick={onCellClick}
        ></MonthView>
      </div>
    </div>
  );
};
